import './Post.module.css';
import s from './Post.module.css';

const Post = (props) => {
    return (
        <div className={s.item}>
            <img
                src="data:image/jpeg;base64,"
                alt="avatar"/>
            { props.message }
            <div>
                <span>Like</span> {props.likesCount}
            </div>
        </div>

    )
}

export default Post;